<template>
<view class="transac mainBox">

		<view class="sacCon">
			<view class="sacTit">
				<view class="titLft">
					<view @click="titMenuEvent">
						<span class="titMenu i-menu" ></span>
						<text>{{titMenu}}</text>
					</view>
					<view class="titMenuList" v-if="isTitMenu">
						<text v-for="(item, index) in titMenuList" :key="item.fid" @click.stop="titMenuListEvent(item)">{{item.fname_sn}}</text>
					</view>
				</view>
				<view class="titRgt">
					<span class="titQuota i-quota"></span>
					<span class="titStar i-star"></span>
				</view>
			</view>
			<view class="sacBody">
				<view class="bodyLft">
					<view class="sacBtn">
						<view class="sacBuyBtn" :class="{'sacBtnChoice': sacOn !== 1}" @click.stop="sacBuyEvent">买入</view>
						<view class="sacSaleBtn" :class="{'sacBtnChoice': sacOn === 1}" @click.stop="sacSaleEvent">卖出</view>
					</view>
					<view class="fixPrice">
						<text>限价</text>
					</view>
					<view class="sacIpt">
						<view v-if="sacOn !== 1">
							<view class="priceIpt" >
								<text class="iptPlus">+</text>
								<input class="ipt" v-model="iptPrice" type="text" placeholder="价位" disabled>
								<text class="iptReduce">-</text>
							</view>
							<view class="quantityIpt priceIpt">
								<text @click="quantityEvent(0)" class="iptPlus">+</text>
								<input class="ipt" v-model="iptQuantity" @blur="iptQuantityEvent" type="number" placeholder="数量">
								<text @click="quantityEvent(1)" class="iptReduce">-</text>
							</view>
							<view class="totalIpt priceIpt">
								<input type="text" v-model="totalNum" placeholder="总额USDT" disabled>
							</view>
						</view>
						
						<view v-if="sacOn === 1">
							<view class="priceIpt" >
								<text class="iptPlus">+</text>
								<input class="ipt" v-model="iptPrice" type="text" placeholder="价位" disabled>
								<text class="iptReduce">-</text>
							</view>
							<view class="quantityIpt priceIpt">
								<text @click="quantityEvent(0)" class="iptPlus">+</text>
								<input class="ipt" v-model="iptQuantity" @blur="iptQuantityEvent" type="number" placeholder="数量">
								<text @click="quantityEvent(1)" class="iptReduce">-</text>
							</view>
							<view class="totalIpt priceIpt">
								<input type="text" v-model="totalNum" placeholder="总额USDT" disabled>
							</view>
						</view>
					</view>
					<view class="sacOpera" @click="sacOperaEvent">
						<view class="operaBtn">{{operaStatus}}</view>
					</view>
					<view class="sacStatus">
						<view class="usable statusView">
							<text>可用</text>
							<text>{{ftotal}} {{unitKind}}</text>
						</view>
						<view class="lockout statusView">
							<text>锁定</text>
							<text>{{ffrozen}} {{unitKind}}</text>
						</view>
						<view class="statusTotal statusView">
							<text>总资产</text>
							<text> {{accAdd(ftotal, ffrozen)}} {{unitKind}}</text>
						</view>
					</view>
				</view>
				<view class="bodyRgt">
					<view class="rgtTit">
						<text>价格(USDT)</text>
						<text>数量</text>
					</view>
					<view class="rgtList">
						<view class="listTop">
							<view v-for="(item, index) in reverseTopList" @click="priceEvent(item)" :key="index" v-if="index < 6">
								<view style="z-index: 9;">
									<text>{{item.price}}</text>
									<text>{{item.quantity}}</text>
								</view>
								<view class="perLine" :style="{width: lineArr[index] + 'rpx'}"></view>
							</view>
						</view>
						<view class="listMid">
							<view class="midQuantity">{{last}}</view>
							<view class="midNum">
								<text class="midNumPrice">¥ {{ accMul(last, cny) }}</text>
								<text class="midNumPer" :style="{color: parseFloat(fupanddown)>0?'#3ba987':'#bd3a3b'}">{{( accMul(fupanddown, 100) ).toFixed(2)}}%</text>
							</view>
						</view>
						<view class="listTop listBtm">
							<view v-for="(item, index) in btmList" @click="priceEvent(item)" :key="index" v-if="index < 6">
								<view style="z-index: 9;">
									<text>{{item.price}}</text>
									<text>{{item.quantity}}</text>
								</view>
								<view class="perLine" :style="{width: btmLineArr[index] + 'rpx'}"></view>
							</view>
						</view>
					</view>
				</view>
			</view>
			
			
			<view class="reminder">
				<view class="remTit">
					<span class="remIco i-warn"></span>
					<text>温馨提示</text>
				</view>
				<view class="remTxt">
					<text>
						交易前请注意查看净值价格，该产品在极端行情下会存在价格趋近于归零的风险，请投资者注意控制风险。
					</text>
				</view>
			</view>
			<view class="entrust">
				<view class="entrustTit">
					<view>
						<text>限价委托</text>
						<view class="record" @click="orderHistoryEvent">
							<span class="recordIco i-record"></span>
							<text>交易记录</text>
						</view>
					</view>
				</view>
			</view>
			<view class="entrustSwitch">
				<view :class="{'enChoice': enOn !== 1}" @click="enSwitchEvent(0)">限价委托</view>
				<view :class="{'enChoice': enOn === 1}" @click="enSwitchEvent(1)" v-if="false">计划委托</view>
			</view>
			<view class="enConTit">
				<view>{{titMenu}}</view>
				<view  v-if="false">撤单</view>
			</view>
			<view class="enConList">
				<view class="enList" v-for="(item, index) in entrustList" :key="item.id">
					<view><text>时间</text><text>{{getLocalTime(item.time)}}</text></view>
					<view><text>价格({{item.buyName}})</text><text>{{item.price}}</text></view>
					<view><text>数量({{item.sellName}})</text><text>{{item.count}}</text></view>
					<view><text>成交总额({{item.sellName}})</text><text>{{item.count}}</text></view>
					<view><text>成交均价</text><text>{{item.avgPrice}}</text></view>
					<view><text>成交量({{item.buyName}})</text><text>{{item.successAmount}}</text></view>
				</view>
			</view>
		</view>
		<uni-popup ref="popup" type="dialog">
		    <uni-popup-dialog iptType="password" title="交易密码" :duration="2000" mode="input" placeholder="请输入交易密码" :before-close="true" @close="close" @confirm="confirm"></uni-popup-dialog>
		</uni-popup>
		
		<input type="text" v-model="moneyDataName" v-show="true">
		<input type="text" v-model="moneyDataFid" v-show="true">
	</view>
</template>

<script>
	import  { accMul, accAdd, checkNum } from '../../utils/common.js'
	import uniPopup from '../../components/uni-popup/uni-popup.vue'
	import uniPopupDialog from '../../components/uni-popup/uni-popup-dialog.vue'
	export default{
		components: { uniPopup, uniPopupDialog },
		props: ['moneyDataName', 'moneyDataFid'],
		data(){
			return{
				sacOn: 0,
				enOn: 0,
				last: 0,
				cny: '',
				fupanddown: 0,
				iptPrice: '',
				ftotal: '0',
				ffrozen: '0',
				iptQuantity: '',
				totalNum: '',
				titMenu: 'BTC / USDT',
				lineArr: [],
				btmLineArr: [],
				enConList: [],
				entrustList: [],
				entrustSell: [],
				titMenuList: [],
				operaStatus: '买入',
				topList: [],
				btmList: [],
				unitKind: 'USDT',
				
				recommendPrizesell: '',
				recommendPrizebuy: '',
				
				getStroageTradePwd: '',
				
				symbolID: '1',
				
				isTitMenu: false,

			}
		},
		methods:{
			/* 交易记录  */
			orderHistoryEvent(){
				uni.navigateTo({
					url: '/pages/transac/transacOrder/transacHistoryOrder?symbol=' + this.symbolID
				})
			},
			titMenuEvent(){
				this.isTitMenu = !this.isTitMenu
			},
			getSocket(moneyDataFid){
				let symbol = ''
				if(moneyDataFid === undefined){
					symbol = 1
				}else{
					symbol = moneyDataFid
				}
				console.log(this.moneyData)
				let isScoket = false
				uni.closeSocket()
				uni.connectSocket({
					url: 'wss://www.dpro.ltd/socket.io/?deep=4&token=dev&symbol=' + symbol + '&EIO=3&transport=websocket',
					method: 'POST',
					success: (res)=>{
						console.log('链接成功', res);
					}
				})
				
				let params = '40/trade'
				uni.onSocketOpen((res)=>{
					isScoket = true
					sendSocketMessage(params)
				})
				function sendSocketMessage(params){
					uni.sendSocketMessage({
						data: params
					})
				}
				
				uni.onSocketMessage((res)=>{
					let data = res.data.replace("42/trade,","")
					if(data.indexOf('entrust-buy') !== -1){
						let obj = []
						let entrustBuy = data.replace('["entrust-buy","[','').replace(']"]','')
						let json =  entrustBuy.split('],[')
						for(let i in json){
							obj.push(json[i].replace('[','').replace(']',''))
						}
						this.btmList = []
						for(let i of obj){
							this.btmList.push({price: i.split(',')[0] , quantity: i.split(',')[1]})
						}
						this.calcLine()
					}else if(data.indexOf('entrust-sell') !== -1) {
						let obj = []
						let entrustSell = data.replace('["entrust-sell","[','').replace(']"]','')
						let json =  entrustSell.split('],[')
						for(let i in json){
							obj.push(json[i].replace('[','').replace(']',''))
						}
						this.topList = []
						for(let i of obj){
							this.topList.push({price: i.split(',')[0] , quantity: i.split(',')[1]})
						}
						
						if(this.topList.length >= 5){
							this.topList = this.topList.slice(0, 6)
						}else{
							this.topList = this.topList.slice(0,this.topList.length-1)
						}
						this.calcLine()
					}else if(data.indexOf('real') !== -1) {
						this.last = JSON.parse( JSON.parse(data)[1]).last
						this.fupanddown = JSON.parse( JSON.parse(data)[1]).fupanddown
					}else if(data.indexOf('entrust-update') !== -1){
						// this.recommendPrizesell = JSON.parse( JSON.parse(data)[1] ).recommendPrizesell
						// this.recommendPrizebuy = JSON.parse( JSON.parse(data)[0] ).recommendPrizebuy
					}
				})
			},
				
			/* 买入切换 */
			sacBuyEvent(){
				this.sacOn = 0
				this.operaStatus = '买入'
				this.unitKind = 'USDT'
				this.titMenu = 'BTC / USDT'
				this.initTitBuyMenuListEvent()
			},
			/* 卖出切换 */
			sacSaleEvent(){
				this.sacOn = 1
				this.operaStatus = '卖出'
				this.unitKind = 'BTC'
				this.initTitSellMenuListEvent()
			},
			confirm(done, value){
				if(!value){
					uni.showToast({
						image: '../../static/images/wrong.png',
						title: '请输入交易密码'
					})
				}else{
					this.tranSellBuy(value)
				}
			},
			close(){
				this.$refs.popup.close()
			},
			sacOperaEvent(){

				this.getTradePwd()
				if(!this.iptPrice){
					uni.showToast({
						image: '../../static/images/wrong.png',
						title: '请输入价位'
					})
				}else if(!this.iptQuantity){
					uni.showToast({
						image: '../../static/images/wrong.png',
						title: '请输入数量'
					})
				}else{
					if(this.getStroageTradePwd == ''){
						this.$refs.popup.open()
					}else{
						this.tranSellBuy(this.getStroageTradePwd.data)
					}
				}
			},
			getTradePwd(){
				uni.getStorage({
					key: 'tradePwd',
					success: (data) => {
						this.getStroageTradePwd = data
					}
				})
			},
			clearTranPwd(){
				let count = 2 * 60 * 60 
				clearInterval(timer)
				let timer = setInterval(()=>{
					count-- 
					if(count == 0){
						uni.removeStorage({
							key: 'tradePwd',
							success: () => {
							}
						})
					}
				}, 1000)
			},
			tranSellBuy(value){
				let url = ''
				let type = ''
				if(this.sacOn == 0){   //买入
					url = '/api/v2/market/buyBtcSubmit'
					type = 'buy'
				}else{                 //卖出
					url = '/api/v2/market/sellBtcSubmit'
					type = 'sell'
				}
				
				let params = {
					symbol: this.symbolID,
					tradeAmount: this.iptQuantity,
					tradeCnyPrice: this.iptPrice,
					type: type,
					tradePwd: value
				}
				
				this.ajaxJson({
					url: url,
					data: params,
					method: 'POST',
					call: (data)=>{
						if(data.code == 200){
							uni.showToast({
								title: data.msg,
								success: () => {
									this.$refs.popup.close()
									uni.setStorage({
										key: 'tradePwd',
										data: value,
										success: () => {
											this.iptPrice = ''
											this.iptQuantity = ''
											this.totalNum = ''
											this.clearTranPwd()
										}
									})
								}
							})
						}else{
							uni.showToast({
								image: '../../static/images/wrong.png',
								title: data.msg,
								success: () => {
									this.$refs.popup.close()
								}
							});
						}
					}
				})
			},
			/*  限定输入数量  */
			iptQuantityEvent(){
				if(checkNum(this.iptQuantity) || this.iptQuantity == ''){
					uni.showToast({
						image: '../../static/images/wrong.png',
						title: '数量格式不正确，请重新输入',
						success: () => {
							this.iptQuantity = ''
						}
					})
				}
				this.calcTotal()
			},
			calcTotal(){
				if(this.iptPrice && this.iptQuantity){
					this.totalNum = accMul(this.iptPrice, this.iptQuantity)
				}else{
					this.totalNum = ''
				}
			},
			/* 数量加减操作 */
			quantityEvent(num){
				if(num === 0){
					this.iptQuantity = parseInt(this.iptQuantity) + 1
					this.calcTotal()
				}else{
					if(parseInt(this.iptQuantity) >= 1){
						this.iptQuantity = parseInt(this.iptQuantity) - 1
					}else{
						this.iptQuantity = 0
					}
					this.calcTotal()
				}
			},
			calcLine(){
				let maxArr = []
				for(let i in this.reverseTopList){
					if(i < 6){
						maxArr.push(this.reverseTopList[i].quantity)
					}
				}
				this.lineArr = []
				let max = Math.max(...maxArr)
				for(let i in maxArr){
					this.lineArr.push( (maxArr[i] / max) * 326 )
				}
				let btmMaxArr = []
				for(let i in this.btmList){
					if(i < 6){
						btmMaxArr.push(this.btmList[i].quantity)
					}
				}
				let btmMax = Math.max(...btmMaxArr)
				this.btmLineArr = []
				for(let i in btmMaxArr){
					this.btmLineArr.push( (btmMaxArr[i] / btmMax) * 326 )
				}
			},
			priceEvent(item){
				this.iptPrice = item.price
				this.calcTotal()
			},
			/* 限价和计划委托切换 */
			enSwitchEvent(index){
				this.enOn = index
			},
			async getEnConList(){
				let params = {id: '48',page: '1',pageSize: '1'}
				
				this.ajaxJson({
					url: '/api/v1/account/opening',
					data: params,
					method: 'POST',
					call: (data)=>{
						this.enConList = data.data.rows
					}
				})
			},
			getRate(){
				uni.getStorage({
					key: 'rate',
					success: (res)=>{
						this.cny = res.data.rate
					},
				})
			},
			getTotal(){
				this.ajaxJson({
					url: '/api/v1/account/balances',
					method: 'POST',
					call: (data)=>{
					}
				})
			},
			getTitMenuList(){
				this.ajaxJson({
					url: '/api/v2/market/coins',
					call: (data)=>{
						this.titMenuList = data.dataMap.USDT
					}
				})
			},
			titMenuListEvent(item){
				this.getSocket(item.fid)
				this.symbolID = item.fid
				let params = {}
				if(this.sacOn == 0){
					params = {
						fvid: item.coinTradeType,
					}
					this.unitKind = 'USDT'
				}else{
					params = {
						fvid: item.coinSellType
					}
					this.unitKind = item.fShortName
				}
				this.getEntrustList()
				this.isTitMenu = false
				this.titMenu = item.fname_sn
				this.ajaxJson({
					url: '/api/v1/account/balances/byFvid',
					data: params,
					call: (data)=>{
						this.ftotal = data.data.ftotal
						this.ffrozen = data.data.ffrozen
					}
				})
			},
			initTitBuyMenuListEvent(){
				this.ajaxJson({
					url: '/api/v1/account/balances/byFvid',
					data: {fvid: '50'},
					call: (data)=>{
						this.ftotal = data.data.ftotal
						this.ffrozen = data.data.ffrozen
					}
				})
			},
			initTitSellMenuListEvent(){
				this.ajaxJson({
					url: '/api/v1/account/balances/byFvid',
					data: {fvid: '51'},
					call: (data)=>{
						this.ftotal = data.data.ftotal
						this.ffrozen = data.data.ffrozen
					}
				})
			},
			getEntrustList(){
				let params = {
					id: this.symbolID
				}
				this.ajaxJson({
					url: '/api/v1/account/opening',
					method: 'POST',
					data: params,
					call: (data)=>{
						this.entrustList = data.data.rows
					}
				})
			}
		},
		computed: {
			reverseTopList() {
				return this.topList.reverse();
			},
			getMoneyData(){
				return this.moneyData
			}
		},
		created() {
			this.getTitMenuList()
			this.getEnConList()
			this.getSocket()
			this.getRate()
			this.getTotal()
			this.initTitBuyMenuListEvent()
			this.getEntrustList()
		},
		/* watch: {
			moneyData:{
				handler(newValue, oldValue) {
					if(this.moneyData.length !== 0){
						this.titMenu = this.moneyData.fname_sn
						this.getSocket(this.moneyData.fid)
						this.symbolID = this.moneyData.fid
					}
				},
				deep: true
			},
			quotatList:{
				handler(newValue, oldValue){
					if(this.quotatList.length !== 0){
						this.titMenu = this.quotatList.fname_sn
						this.getSocket(this.quotatList.fid)
						this.symbolID = this.quotatList.fid
					}
				},
				deep: true
			}
		} */
	}
</script>

<style scoped lang="scss">
	.transac{
		.sacCon{
			.sacTit{
				display: flex;
				justify-content: space-between;
				margin: 50rpx 35rpx 0;
				.titLft{
					.titMenu:before{
						color: #fff;
					}
					text{
						color: #fff;
						margin-left: 35rpx;
					}
					.titMenuList{
						position: absolute;
						z-index: 9;
						left: 60rpx;
						border: 1px solid #676869;
						background-color: #303030;
						display: flex;
						flex-direction: column;
						padding: 5rpx 10rpx;
						text{
							margin-left: 0;
							font-size: 24rpx;
							margin: 5rpx 0;
						}
					}
				}
				.titRgt{
					.titQuota:before{
						color: #BD3A3B;
					}
					.titStar{
						margin-left: 74rpx;
					}
					.titStar:before{
						color: #403F45;
					}
				}
			}
			.sacBody{
				display: flex;
				justify-content: space-between;
				margin: 0 20rpx;
				.bodyLft{
					.sacBtn{
						margin-top: 55rpx;
						background-color: #303030;
						display: flex;
						justify-content: space-between;
						align-items: center;
						border-radius: 30rpx;
						view{
							text-align: center;
							line-height: 60rpx;
							height: 60rpx;
							width: 184rpx;
						}
						.sacBtnChoice{
							border-radius: 30rpx;
							background: linear-gradient(#EA4F6E, #EB5F60);;
							color: #fff;
						}
					}
					.fixPrice{
						margin: 38rpx auto;
						text{
							color: #FFFFFF;
							font-size: 24rpx;
							line-height: 24rpx;
						}
					}
					.sacIpt{
						view{
							.quantityIpt, .totalIpt{
								margin-top: 55rpx;
							}
							.totalIpt{
								input{
									text-align: center;
								}
							}
							.priceIpt{
								display: flex;
								justify-content: space-between;
								align-items: center;
								width: 382rpx;
								height: 80rpx;
								border: 1px solid #6A3434;
								border-radius: 10rpx;
								text{
									width: 80rpx;
